body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #ffffff;
}

.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: white;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
}

.form-container {
  background-color: white;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 90%;
}

h2 {
  margin-bottom: 20px;
  color: #333;
  font-size: 24px;
}

.form input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

.form input:focus {
  border-color: #007bff;
  outline: none;
}

/* Buttons */
.primary-button,
.google-button {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
  font-weight: bold;
}

.primary-button {
  background-color: #007bff;
  color: white;
}

.primary-button:hover {
  background-color: #0056b3;
}

.google-button {
  background-color: #db4437;
  color: white;
}

.google-button:hover {
  background-color: #b3392d;
}

.form-toggle p {
  margin: 15px 0;
  font-size: 14px;
  color: #555;
}

.form-toggle span {
  color: #007bff;
  cursor: pointer;
  font-weight: bold;
}

.form-toggle span:hover {
  text-decoration: underline;
}

.input-error {
  border: 1px solid red;
}

.error-message {
  color: red;
  margin-top: 0.5rem;
  font-size: 0.9rem;
}


/* tablets */
@media (max-width: 1024px) {
  .form-container {
    padding: 30px;
    max-width: 350px;
    border-radius: 12px;
  }

  h2 {
    font-size: 20px;
  }

  .form input {
    font-size: 14px;
    padding: 8px;
  }

  .primary-button,
  .google-button {
    font-size: 14px;
    padding: 10px;
  }
}

/* phones */
@media (max-width: 768px) {
  .form-container {
    padding: 20px;
    max-width: 85%;
    border-radius: 10px;
  }

  h2 {
    font-size: 18px;
  }

  .form input {
    font-size: 14px;
    padding: 8px;
  }

  .primary-button,
  .google-button {
    font-size: 14px;
    padding: 10px;
  }

  .form-toggle p {
    font-size: 12px;
  }
}

/* small phones */
@media (max-width: 480px) {
  .form-container {
    padding: 15px;
    max-width: 95%;
    border-radius: 8px;
  }

  h2 {
    font-size: 16px;
  }

  .form input {
    font-size: 12px;
    padding: 6px;
  }

  .primary-button,
  .google-button {
    font-size: 12px;
    padding: 8px;
  }

  .form-toggle p {
    font-size: 12px;
  }
}
